import React from 'react';
import {Link} from "react-router-dom";

const IncidentItem = ({title, to, img, color}) => {
    return (
        <>
            <Link to={to} className="incident-item">
                <div className="incident-img-wrapper">
                    <img src={img} alt={title} style={{border: '4px solid ' + color}}/>
                </div>
                <p className="incident-desc">{title}</p>
            </Link>
        </>
    )
};

export default IncidentItem;
