import React from 'react';
import Header from "./Header";
import {MDBBtn, MDBContainer, MDBInput, MDBNotification} from "mdbreact";

class Success extends React.Component {
    state = {
        clicked: false
    }

    click = () => this.setState({clicked: true})

    render() {
        const notification = !this.state.clicked ? undefined :
            <MDBNotification
                show
                fade
                iconClassName="text-default"
                title="R@W"
                message="Email verstuurd"
                style={{
                    position: "fixed",
                    bottom: "10px",
                    left: "10px",
                    right: "10px",
                    zIndex: 9999
                }}
            />;
        return (
            <>
                <Header text={"Bedankt!"} backLink={"/"}/>
                <MDBContainer>
                    <p>Bedankt! De leidinggevende wordt op de hoogte gebracht.</p>
                    <br/>
                    <MDBInput label="Email"/>
                    <MDBBtn onClick={this.click} block>Stuur bevestiging</MDBBtn>
                    {notification}
                </MDBContainer>
            </>
        )
    }
}

export default Success;
