import React from 'react';
import {MDBInput} from "mdbreact";

class SpecifyOrNoQuestion extends React.Component {
    state = {
        radio: 2
    }

    onClick = nr => () => {
        this.setState({
            radio: nr
        });
    }

    render() {
        const {question} = this.props;
        const {radio} = this.state;
        const verklaarInput = radio === 0 ?
            <MDBInput label="Verklaar"/> :
            undefined;

        return (
            <>
                <p>{question}</p>
                <form>
                    <div className="custom-control custom-radio" onClick={this.onClick(0)}>
                        <input type="radio" id="ja" name="radio" className="custom-control-input"/>
                        <label className="custom-control-label" htmlFor="ja">Ja</label>
                    </div>
                    {verklaarInput}
                    <div className="custom-control custom-radio" onClick={this.onClick(1)}>
                        <input type="radio" id="nee" name="radio" className="custom-control-input"/>
                        <label className="custom-control-label" htmlFor="nee">Nee</label>
                    </div>
                </form>
            </>
        )
    }
}

export default SpecifyOrNoQuestion;
