import React from 'react';

const OptionQuestion = ({question, options}) => {
    return (
        <>
            <p>{question}</p>
            <form>
                {options.map((option, i) => {
                    return (
                        <div className="custom-control custom-radio" key={i}>
                            <input type="radio" id={"r" + i} name="radio" className="custom-control-input"/>
                            <label className="custom-control-label" htmlFor={"r" + i}>{option}</label>
                        </div>
                    )
                })}
            </form>
        </>
    )
};

export default OptionQuestion;
