import React from 'react';
import {MDBBtn, MDBContainer} from "mdbreact";
import {Link} from "react-router-dom";
import Header from "./Header";

const Type = () => {
    return (
        <>
            <Header text={"Wat wilt u melden?"} backLink={"/"}/>
            <MDBContainer>
                <Link to={"/incident"}>
                    <MDBBtn block>Incident</MDBBtn>
                </Link>
                <p>bv: Val, prik, etc.</p>
                <Link to={"/incident"}>
                    <MDBBtn block>Bijna-incident</MDBBtn>
                </Link>
                <p>Geen schade</p>
            </MDBContainer>
        </>
    )
};

export default Type;
