import React from 'react';
import Header from "./Header";
import {MDBContainer} from "mdbreact";

const Vision = () => {
    return (
        <>
            <Header text={"Onze visie"} backLink={"/"}/>
            <MDBContainer>
                <p>Wij staan voor het bieden van professionele zorg op maat. Alleen is dit pas mogelijk wanneer elk lid
                    van
                    het verpleegkundig team in een no-blame culture zijn/haar werk op een veilige manier kan uitvoeren.
                    Duidelijke communicatie en het respecteren van ieders privacy dragen wij hoog in het vaandel.
                    Taboedoorbrekende tools en zorgtechnologie gaan hand in hand, dit met preventie als hoofddoel. Het
                    uitvoeren van verpleegkundige handelingen in een veilige werkomgeving heeft een grote impact op het
                    welzijn van de zorgverleners, alsook daarop aansluitend op het welbevinden van de zorgvrager.
                    Toekomstgericht streven wij naar een veiligere gezondheidssector voor elk individu die zorg
                    ondergaat en
                    die zorg verleent.</p>
                <p>De visie van onze opdrachtgever, UZ Leuven, heeft vele raakvlakken met de visie die wij hanteren. UZ
                    Leuven wil grenzen verleggen naar een betere gezondheidszorg. Een betere gezondheidszorg is naar
                    onze
                    mening pas mogelijk wanneer deze op een veilige manier kan verlopen. Innoveren op vlak van
                    zorgtechnologie is onze manier om grensverleggend tewerk te gaan.</p>
                <p>Om zowel gebruiker als klant op de hoogte te brengen van onze visie, is deze steeds terug te vinden
                    op
                    onze applicatie.</p>
            </MDBContainer>
        </>
    )
};

export default Vision;
