import React from 'react';
import {MDBCol, MDBContainer, MDBRow} from "mdbreact";
import Header from "./Header";
import IncidentItem from "./IncidentItem";

const incidents = [
    {title: 'Agressie', to: '/questions', img: '/incident/fist.png', color: '#2BBBAD'},
    {title: 'Arbeidsongeval', to: '/questions', img: '/incident/helmet.png', color: '#4285F4'},
    {title: 'Brand', to: '/questions', img: '/incident/flame.png', color: '#aa66cc'},
    {title: 'EHBO', to: '/questions', img: '/incident/ehbo.png', color: '#ff4444'},
    {title: 'Nood', to: '/questions', img: '/incident/sos.png', color: '#ffbb33'},
    {title: 'Prik-, Snij-, Spatongeval', to: '/questions', img: '/incident/blood.png', color: '#00C851'},
    {title: 'Ziekenhuis noodplanning', to: '/questions', img: '/incident/hospital.png', color: '#33b5e5'},
    {title: 'Contact schadelijke stoffen', to: '/questions', img: '/incident/toxic.png', color: '#00695c'},
    {title: 'Andere', to: '/questions', img: '/incident/help.png', color: '#0d47a1'},
];

const Incident = () => {
    return (
        <>
            <Header text={"Welk type incident?"} backLink={"/type"}/>
            <MDBContainer>
                <MDBRow>
                    {incidents.map((incident, i) => {
                        return (
                            <MDBCol key={i} size={"4"} className="incident-col">
                                <IncidentItem {...incident} />
                            </MDBCol>
                        )
                    })}
                </MDBRow>
            </MDBContainer>
        </>
    )
};

export default Incident;
