import React from 'react';
import Header from "./Header";
import OptionQuestion from "./questions/OptionQuestion";
import OpenQuestion from "./questions/OpenQuestion";
import DateTimeQuestion from "./questions/DateTimeQuestion";
import {MDBBtn, MDBContainer, MDBIcon} from "mdbreact";
import OptionOrOtherOption from "./questions/OptionOrOtherOption";
import SpecifyOrNoQuestion from "./questions/SpecifyOrNoQuestion";
import MultipleOptionQuestion from "./questions/MultipleOptionQuestion";
import {Link} from "react-router-dom";
import MultipleOptionOrOtherOption from "./questions/MultipleOptionOrOtherOption";

const yesNoOptions = ["Ja", "Nee"];
const options1 = ["Struikelen", "Vallen", "Voorwerp"];
const options2 = ["Gebruikelijke werkplek", "Occasionele werkplek", "Onderweg naar de werkplek"];
const options3 = ["Enkel lichamelijke schade", "Materiële en lichamelijke schade"];
const options4 = ["Geen", "IDEWE", "Spoed", "Dokter", "Apotheek", "Nieuwe bril", "Prothese"];
const options5 = ["Zoveel mogelijk laten uitbloeden", "Goed reinigen en ontsmetten met chloorhexidine 0,5% in alcohol 70°"];

const questions = [
    <OptionQuestion question={"Was er een getuige aanwezig?"} options={yesNoOptions}/>,
    <OpenQuestion question={"Wat was de locatie?"} label={"Locatie"}/>,
    <DateTimeQuestion question={"Wanneer gebeurde het incident?"}/>,
    <OpenQuestion question={"Geef een korte omschrijving van de gebeurtenis."}/>,
    <OptionOrOtherOption question={"Welke afwijkende gebeurtenis heeft geleid tot het ongeval?"} options={options1}/>,
    <OpenQuestion question={"Welke specifieke activiteit was u aan het uitvoeren toen het incident zich voordeed?"}
                  label={"Activiteit"}/>,
    <OptionOrOtherOption question={"Aan welke werkplek stond u tijdens het incident?"} options={options2}/>,
    <OptionQuestion question={"Welke schade liep u op als gevolg van het incident?"} options={options3}/>,
    <MultipleOptionOrOtherOption question={"Welke kosten in verband met deze gebeurtenis wilt u vergoed zien?"}
                                 options={options4}/>,
    <SpecifyOrNoQuestion question={"Droeg u beschermmiddelen?"}/>,
    <SpecifyOrNoQuestion question={"Had de gebeurtenis vermeden kunnen worden?"}/>,
    <MultipleOptionQuestion question={"Welke verzorgingsmaatregelen heeft u genomen"} options={options5}/>,
]

class Questions extends React.Component {
    state = {
        index: 0
    }

    previous = () => this.setState({index: this.state.index - 1})
    next = () => this.setState({index: this.state.index + 1})

    previousDisabled = () => this.state.index <= 0;
    nextDisabled = () => this.state.index >= questions.length - 1;

    render() {
        const nextButton = this.nextDisabled() ?
            <Link to={"/success"}>
                <MDBBtn color="light-blue"><MDBIcon icon={"fas fa-paper-plane"}/></MDBBtn>
            </Link>
            :
            <MDBBtn disabled={this.nextDisabled()} onClick={this.next}>
                <MDBIcon icon={"fa fa-chevron-right"}/>
            </MDBBtn>
        ;
        return (
            <>
                <Header text={"Vragen"} backLink={"/Incident"}/>
                <MDBContainer>
                    {questions[this.state.index]}
                    <div className="arrows">
                        <MDBBtn disabled={this.previousDisabled()} onClick={this.previous}>
                            <MDBIcon icon={"fa fa-chevron-left"}/>
                        </MDBBtn>
                        <p className="mb-0">{this.state.index + 1} / {questions.length}</p>
                        {nextButton}
                    </div>
                </MDBContainer>
            </>
        )
    }
}

export default Questions;
