import React from 'react';
import {MDBInput} from "mdbreact";

class OptionOrOtherOption extends React.Component {
    state = {
        radio: undefined
    }

    onClick = nr => () => {
        this.setState({
            radio: nr
        });
    }

    render() {
        const {question, options} = this.props;
        const {radio} = this.state;
        const andereInput = radio === 0 ?
            <MDBInput label="Andere"/> :
            undefined;

        return (
            <>
                <p>{question}</p>
                <form>
                    {options.map((option, i) => {
                        return (
                            <div className="custom-control custom-radio" key={i} onClick={this.onClick(i+1)}>
                                <input type="radio" id={"r" + i} name="radio" className="custom-control-input"/>
                                <label className="custom-control-label" htmlFor={"r" + i}>{option}</label>
                            </div>
                        )
                    })}
                    <div className="custom-control custom-radio" onClick={this.onClick(0)}>
                        <input type="radio" id="andere" name="radio" className="custom-control-input"/>
                        <label className="custom-control-label" htmlFor="andere">Andere</label>
                    </div>
                    {andereInput}
                </form>
            </>
        )
    }
}

export default OptionOrOtherOption;
