import React from 'react';
import {MDBInput} from "mdbreact";

class MultipleOptionOrOtherOption extends React.Component {
    state = {
        andere: false
    }

    onClick = () => {
        this.setState({
            andere: !this.state.andere
        });
    };

    render() {
        const {question, options} = this.props;
        const {andere} = this.state;
        const andereInput = andere ?
            <MDBInput label="Andere"/> :
            undefined;

        return (
            <>
                <p>{question}</p>
                <form>
                    {options.map((option, i) => {
                        return (
                            <div className="custom-control custom-checkbox" key={i}>
                                <input type="checkbox" id={"r" + i} name="radio" className="custom-control-input"/>
                                <label className="custom-control-label" htmlFor={"r" + i}>{option}</label>
                            </div>
                        )
                    })}
                    <div className="custom-control custom-checkbox">
                        <input type="checkbox" id="andere" name="radio" className="custom-control-input" onChange={this.onClick}/>
                        <label className="custom-control-label" htmlFor="andere">Andere</label>
                    </div>
                    {andereInput}
                </form>
            </>
        )
    }
}

export default MultipleOptionOrOtherOption;
