import React from 'react';
import {MDBBtn, MDBCol, MDBContainer, MDBIcon, MDBInput, MDBRow} from "mdbreact";
import {Link} from "react-router-dom";

const Home = () => {
    return (
        <MDBContainer>
            <MDBRow>
                <MDBCol>
                    <form>
                        <div className="text-center">
                            <img src="/logo.png" alt="logo"/>
                        </div>
                        <div className="grey-text">
                            <MDBInput label="Type your email" icon="envelope" group type="email" validate error="wrong"
                                      success="right"/>
                            <MDBInput label="Type your password" icon="lock" group type="password" validate/>
                        </div>
                        <div className="text-center mt-4">
                            <Link to={"/type"}>
                                <MDBBtn block={true} type="submit">Login</MDBBtn>
                            </Link>
                        </div>
                    </form>
                    <br/>
                    <hr/>
                    <div className="d-flex justify-content-center">
                        <img src="/ucll.png" className="logo" alt="logo"/>
                        <img src="/jensbnt.png" className="logo" alt="logo"/>
                        <img src="/3dc.png" className="logo" alt="logo"/>
                        <img src="/vla.png" className="logo" alt="logo"/>
                    </div>
                </MDBCol>
            </MDBRow>
            <div className="vision">
                <Link to={"/vision"}>
                    <MDBIcon icon={"fas fa-info"}/>
                </Link>
            </div>
        </MDBContainer>
    )
};

export default Home;
